import { FC, useMemo, memo } from "react";
import classNames from "classnames";
import { Collapse } from 'antd';

import TrustScoreItem from "./TrustScoreItem";
import styles from './TrustScoreTable.module.css';
import ITrustScore from "../../types/ITrustScore";

interface Props {
  data: ITrustScore
}

const { Panel } = Collapse;

const TrustScoreTable: FC<Props> = ({ data }) => {
  const status = useMemo(() => {
    if (data.total <= 30) return 'negative';
    if (data.total > 30 && data.total <= 60) return 'neutral';
    return 'positive';
  }, [data.total]);
  const percent = useMemo(() => {
    const num = Number(data.total).toFixed(1);
    return num.endsWith('.0') ? num.slice(0, -2) : num;
  }, [data.total]);
  return (
    <div className={styles.tableWrapper}>
      <div className={styles.chart}>
        <div className={styles.title}>Trust score</div>
        <div className={styles['single-chart']}>
          <svg viewBox="0 0 36 36" className={classNames(styles['circular-chart'], styles[status])}>
            <path className={styles['circle-bg']}
              d="M18 2.0845
          a 15.9155 15.9155 0 0 1 0 31.831
          a 15.9155 15.9155 0 0 1 0 -31.831"
            />
            <path className={styles.circle}
              strokeDasharray={`${percent}, 100`}
              d="M18 2.0845
          a 15.9155 15.9155 0 0 1 0 31.831
          a 15.9155 15.9155 0 0 1 0 -31.831"
            />
            <text x="18" y="20.35" className={classNames(styles.percentage, styles[status])}>{percent}</text>
          </svg>
        </div>
      </div>
      <div className={styles.collapse}>
        <Collapse ghost expandIconPosition="right">
          <Panel header="Metrics" key="1" className="collapse-content">
            <ul className={styles.table}>
              {data.standards.map((standard) => (
                <li className={styles.item} key={standard.name}>
                  <TrustScoreItem {...standard} key={standard.name} />
                </li>
              ))}
            </ul>
          </Panel>
        </Collapse>
      </div>
    </div>
  );
}

export default memo(TrustScoreTable);
