import classNames from "classnames";
import { FC, useMemo, memo } from "react";
import { IStandard } from "../../types/ITrustScore";

import styles from './TrustScoreItem.module.css';

const TrustScoreItem: FC<IStandard> = ({
  name,
  score,
  value,
  max,
  reason,
}) => {
  const status = useMemo(() => {
    const percent = (score / max) * 100;
    if (percent <= 30) return 'negative';
    if (percent > 30 && percent <= 60) return 'neutral';
    return 'positive';
  }, [score, max]);
  return (
    <div className={styles.item}>
      <div className={styles.title}>
        <div className={styles.text}>{name} {(typeof value === 'number' || typeof value === 'string') && !['Logo', 'Twitter', 'Github', 'Telegram', 'Website', 'Description'].includes(name) && value && <span className={styles.singleValue}>({value})</span>}</div>
        <div className={classNames(styles.score, styles[status])}>
          <span>{Number(score).toFixed(2).endsWith('.00') ? Number(score).toFixed(2).slice(0, -3) : Number(score).toFixed(2)}</span>
          <span>/</span>
          <span>{max}</span>
        </div>
      </div>
      <div className={styles.desc}>{reason}</div>
      <div className={styles.values}>
        {Array.isArray(value) && (
          <>{value.map(([t, v]) => (
            <div>
              <span>{t}: </span>
              <span>{v}</span>
            </div>
          ))}</>
        )}
      </div>
    </div>
  );
}

export default memo(TrustScoreItem);
