import { useWeb3React } from "@web3-react/core";
import { useEffect, useState } from "react";

import multicall from "../utils/multicall";
import { useWeb3 } from "../packages/web3/hooks";
import { useTokenManagerContract } from "./useContract";

const ERC20Abi = require("../abi/ERC20Abi.json");

export default function useTokenInfo(address?: string) {
  const [tokenInfo, setTokenInfo] = useState<{
    name: string;
    decimals: string;
    symbol: string;
    totalSupply: string;
    isTokenGenerated: boolean;
  }>();
  const [loading, setLoading] = useState<boolean>(false);
  const web3 = useWeb3();
  const tokenManagerContract = useTokenManagerContract(web3);
  const { chainId } = useWeb3React();

  useEffect(() => {
    (async () => {
      if (!address || !tokenManagerContract) return;
      try {
        setLoading(true);
        const calls = ["name", "symbol", "decimals", "totalSupply"].map(
          (method) => ({
            address: address,
            name: method,
          })
        );
        const [[name], [symbol], [decimals], [totalSupply]] = await multicall(
          ERC20Abi,
          calls,
          {
            web3,
          },
          chainId
        );
        const isTokenGenerated = await tokenManagerContract.methods
          .isTokenGenerated(address)
          .call();
        setTokenInfo({
          name,
          symbol,
          decimals: decimals.toString(),
          totalSupply: totalSupply.toString(),
          isTokenGenerated,
        });
      } catch (e) {
        // console.log(e);
      } finally {
        setLoading(false);
      }
    })();
  }, [web3, address, chainId, tokenManagerContract]);

  return {
    token: tokenInfo,
    loading,
  };
}
